<template>
  <div :class="{ 'dark-mode': isDarkMode }">
    <header v-if="isLoggedIn">
      <router-link to="/">조기헌의 포트폴리오</router-link>
      <br />
      "게임 개발자가 되고 싶은 학생의 포트폴리오입니다."
    </header>

    <Nav v-if="isLoggedIn" />

    <Login v-else :handleLogin="handleLogin" />
    
    <footer v-if="isLoggedIn">
      <i>
        Copyright 2023. 지은이 all rights reserved.
        <br />
        연락처: 010-4095-5627
      </i>
      <div class="dark-mode-toggle-container">
        <DarkModeToggle :isDarkMode="isDarkMode" :toggleMode="toggleDarkMode" />
      </div>
    </footer>
  </div>
