<script>
import Nav from './Component/MyNav.vue';
import DarkModeToggle from './DarkModeToggle.vue';
import Login from './Login.vue';

export default {
  components: {
    Nav,
    DarkModeToggle,
    Login,
  },

  data() {
    return {
      isDarkMode: false,
      isLoggedIn: false,
    };
  },
  methods: {
    toggleDarkMode() {
      this.isDarkMode = !this.isDarkMode;
    },
    handleLogin(id, pw) {
      if (id === 'chogh' && pw === '1234') {
        this.isLoggedIn = true;
        return true;
      } else {
        this.isLoggedIn = false;
        return false;
      }
    },
  },
};
</script>

