<!-- Login.vue -->
<template>
    <div class="login-container">
      <input
        type="text"
        placeholder="아이디를 입력하세요"
        v-model="id"
      />
      <input
        type="password"
        placeholder="비밀번호를 입력하세요"
        v-model="pw"
      />
      <button class="login-button" @click="onSubmit">
        로그인
      </button>
      <div v-if="showLoginMessage" class="login-message"></div>
    </div>
  </template>
  
  <script>
  export default {
    data() {
      return {
        id: '',
        pw: '',
        showLoginMessage: false,
      };
    },
    methods: {
      onSubmit() {
        const loginSuccess = this.handleLogin(this.id, this.pw);
        if (loginSuccess) {
          this.showLoginMessage = true;
        } else {
          // 로그인 실패 시 알림 표시
          alert('아이디나 비밀번호가 틀렸습니다. 다시 확인해주세요.');
        }
      },
    },
    props: {
      handleLogin: {
        type: Function,
        required: true,
      },
    },
  };
  </script>
  
  <style scoped>
  .login-container {
    display: flex;
    flex-direction: column;
    width: 300px; /* 변경된 크기 */
    height: 300px; /* 변경된 크기 */
    margin: 0 auto;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  
  input {
    margin: 10px 0;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  button.login-button {
    background-color: #4caf50;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .login-message {
    margin-top: 10px;
    color: green;
  }
  </style>