<!-- MyCareers.vue -->
<template>
  <div id="career">
    <div class="tab-container">
      <button
        v-for="(career, i) in careers"
        :key="i"
        @click="contentChange(i)"
        :class="{ 'tab': true, 'active': index === i }"
      >
        {{ career.tab }}
      </button>
    </div>

    <div v-if="careers[index] && careers[index].content">
      <component :is="careers[index].content"></component>
    </div>
  </div>
</template>

<script setup>
import { ref } from 'vue';
import careers from './Data/MyCareers';

const index = ref(0);

const contentChange = (selectedIndex) => {
  index.value = selectedIndex;
};
</script>

<style scoped>
@import "./CSS/Career.css";

.tab-container {
  display: flex;
}

.tab {
  cursor: pointer;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-right: 5px;
}

.tab.active {
  background-color: rgb(132,123,123);
}
</style>