<!-- MyProjects.vue -->
<template>
  <div id="project">
    <div class="tab-container">
      <button
        v-for="(project, i) in projects"
        :key="i"
        @click="contentChange(i)"
        :class="{ 'tab': true, 'active': index === i }"
      >
        {{ project.tab }}
      </button>
    </div>

    <div v-if="projects[index] && projects[index] && projects[index].content">
      <component :is="projects[index].content"></component>
    </div>
  </div>
</template>

<script setup>
import { ref } from 'vue';
import projects from './Data/MyProjects';

const index = ref(0);

const contentChange = (selectedIndex) => {
  index.value = selectedIndex;
};
</script>

<style scoped>
@import "./CSS/Project.css";

.tab-container {
  display: flex;
}

.tab {
  cursor: pointer;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-right: 5px;
}

.tab.active {
  background-color: rgb(132,123,123)
}
</style>